.movie-container {
  min-height: 100vh;
  height: fit-content;
  background-color: #363636;
}

.movie-formContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  /* background-color: red; */
}
.movie-form {
  width: 80%;
  padding: 0 20px 10px 20px;
  margin-bottom: 70px;
  border-radius: 10px;
  background-color: #151515;
}
