.movie-rating {
  display: flex;
  height: 40px;
  align-items: center;
  justify-content: center;
  /* background-color: yellow; */
}
.movie-rating > span {
  display: flex;
  justify-content: flex-end;
  /* background-color: green; */
}
.movie-rating > h2 {
  width: 3%;
  margin-left: 10px;
  color: white;
  /* background-color: lime; */
}

.movie-convert-button {
  display: flex;
  align-items: center;
  padding: 0 10px 0 10px;
  background-color: var(--pink);
  color: white;
  cursor: pointer;
}
